.weekly-trends {
    padding-top: 40px;
    padding-bottom: 32px;

    @media screen and (min-width: 768px) {
        padding-top: 52px;
        padding-top: 44px;

    }

    @media screen and (min-width: 1280px) {
        padding-top: 80px;
        padding-top: 60px;
    }
}


.title-grup {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 28px;
    @media screen and (min-width: 768px) {
        margin-bottom: 24px;
        
    }
}

.title-grup__link{
    font-weight: 500;
    font-size: 16px;
    line-height: 1.18;

    color: var(--accent-color);
    @media screen and (min-width: 768px) {
        font-size: 24px;
        line-height: 1.18;

    }

    @media screen and (min-width: 1280px) {
        font-size: 28px;

    }
}
.title-grup__name {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.18;

    text-transform: uppercase;
    color: var(--main-light-color);
    @media screen and (min-width: 768px) {
        font-size: 24px;
        line-height: 1.18;

    }

    @media screen and (min-width: 1280px) {
        font-size: 28px;

    }
}

@media screen and (max-width: 768px) {
    .films-list {
        height: 400px;
        overflow: hidden;
    }
}




